.settings {
  margin: 0 auto;
  max-width: 800px; }
  .settings-content {
    padding: 20px 10px 0; }
    .settings-content-header {
      text-align: left;
      font-size: 20px !important; }
    .settings-content-section {
      margin-top: 15px;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between; }
      .settings-content-section .caption {
        font-size: 16px;
        text-align: left;
        width: 70%; }
      .settings-content-section .button {
        text-align: left;
        width: 200px !important; }
      .settings-content-section .button:hover,
      .settings-content-section .button:active {
        color: white;
        border-color: white; }
