  border: #282828 solid 1px;
  margin: 30px auto 0;
  max-width: 700px; }
  .meme-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px; }
    .meme-header-channel {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-left: 10px;
      height: 30px; }
      .meme-header-channel-name {
        font-size: 12px; }
      .meme-header-channel-ago {
        font-size: 10px;
        color: grey; }
  .meme-caption {
    padding: 0 10px 10px;
    margin-top: 10px; }
