.not-found {
  position: fixed;
  top: 50%;
  left: 50%; }
  @media only screen and (max-width: 450px) {
    .not-found {
      transform: translate(-50%, -60%); } }
  @media only screen and (min-width: 450px) {
    .not-found {
      transform: translate(-50%, -50%);
      display: flex;
      align-items: center; }
      .not-found .content {
        margin-left: 30px; } }
  .not-found .image {
    width: 320px;
    height: 289px;
    background: url("../../images/404.png") no-repeat center;
    background-size: cover; }
